import { ListBoxKbdStrategyBase } from "./dx-list-box-kbd-strategy";
import { KeyCode, KeyUtils } from "@devexpress/utils/lib/utils/key";
import { ListBoxTableKbdStrategy } from "./dx-list-box-table-kbd-strategy";
import { ListBoxListKbdStrategy } from "./dx-list-box-list-kbd-strategy";
import { ListBoxLayoutHelper, ListBoxNavigationAreaType } from "../dx-list-box-layout-helper";
import { ListBoxSearchKbdStrategy } from "./dx-list-box-search-kbd-strategy";
export class ListBoxRootKbdStrategy extends ListBoxKbdStrategyBase {
    constructor(listBox) {
        super(listBox, listBox);
    }
    getShortcutContext() {
        return {
            IsMacOSPlatform: this.isMacOSPlatform
        };
    }
    queryItems() {
        return new Array(this.getSearchBox(), this.getList(), this.getTable());
    }
    createItemStrategy(itemElement) {
        switch (ListBoxLayoutHelper.getNavigationAreaType(itemElement)) {
            case ListBoxNavigationAreaType.SearchBox:
                return new ListBoxSearchKbdStrategy(this.component, itemElement);
            case ListBoxNavigationAreaType.Table:
                return new ListBoxTableKbdStrategy(this.component, itemElement);
            case ListBoxNavigationAreaType.List:
                return new ListBoxListKbdStrategy(this.component, itemElement);
        }
        return null;
    }
    moveToPrevItem() {
        if (this.selectedItemIndex > 0)
            super.moveToPrevItem();
        else
            this.leaveBackward();
    }
    moveToNextItem() {
        if (this.selectedItemIndex < this.itemCount - 1)
            super.moveToNextItem();
        else
            this.leaveForward();
    }
    handleKeyDown(evt) {
        const keyCode = KeyUtils.getEventKeyCode(evt);
        if (keyCode === KeyCode.Tab)
            return this.handleTabKeyDown(evt);
        if (keyCode === KeyCode.Up && evt.ctrlKey) {
            this.moveToPrevItem();
            return true;
        }
        if (keyCode === KeyCode.Down && evt.ctrlKey) {
            this.moveToNextItem();
            return true;
        }
        return false;
    }
    handleTabKeyDown(evt) {
        if (!super.handleTabKeyDown(evt, evt.altKey)) {
            if (evt.shiftKey)
                this.leaveBackward();
            else
                this.leaveForward();
        }
        return true;
    }
}
