import { KeyCode, KeyUtils } from "@devexpress/utils/lib/utils/key";
import { KeyboardNavigationStrategy } from "../../../common/keyboard-navigation/keyboard-navigation-strategy";
import { DxScrollViewer, ScrollViewerVisibleElementChangedEvent, VerticalAlign, HorizontalAlign } from "../../../scroll-viewer/dx-scroll-viewer";
import { FocusUtils } from "../../../common/keyboard-navigation/keyboard-navigation-utils";
import { ListBoxAttributes } from "../dx-list-box-attributes";
import DomHelper from "../../../utils/domhelper";
import { ListBoxKbdNavigationUtils } from "./dx-list-box-kbd-navigation-utils";
import { addFocusHiddenAttribute, removeFocusHiddenAttribute } from "../../../focus-utils";
import { ListBoxSelectors } from "../dx-list-box-selectors";
export class ListBoxKbdStrategyBase extends KeyboardNavigationStrategy {
    constructor(component, targetElement) {
        super(component.getKeyboardNavigator(), targetElement);
        this.component = component;
    }
    getTable() {
        return this.component.getTable();
    }
    getList() {
        return this.component.getList();
    }
    getSearchBox() {
        return this.component.getSearchBox();
    }
    getScrollViewer() {
        return this.component.getScrollViewer();
    }
    getScrollViewerContent() {
        return this.component.getScrollViewerContent();
    }
    getColumnInfoByCell(element) {
        return this.component.getColumnInfoByCell(element);
    }
    get isFocusedRowEnabled() {
        return this.component.enableFocusedRow;
    }
    get isMultipleSelectionEnabled() {
        return this.component.enableMultipleSelection;
    }
    canSwitchToNestedContentMode() {
        return true;
    }
    handleTabKeyDown(evt, allowAltKey = false) {
        if (!allowAltKey && evt.altKey)
            return false;
        if (evt.shiftKey) {
            if (this.firstItemSelected)
                return false;
            this.moveToPrevItem();
        }
        else {
            if (this.lastItemSelected)
                return false;
            this.moveToNextItem();
        }
        return true;
    }
}
export class ListBoxItemKbdStrategyBase extends ListBoxKbdStrategyBase {
    constructor(containerStrategy, listBox, targetElement) {
        super(listBox, targetElement);
        this.lockScroll = 0;
        this.containerStrategy = containerStrategy;
    }
    isImmediatelyClickEnabled() {
        return false;
    }
    get preventScrollOnFocus() {
        return this.lockScroll > 0;
    }
    getVisibleIndex() {
        return DomHelper.getAttributeIntValue(this.targetElement, ListBoxAttributes.VisibleIndexAttributeName, -1);
    }
    leave() {
        FocusUtils.removeTabIndex(this.selectedItemElement);
    }
    lockScrollOnFocus() {
        this.lockScroll++;
    }
    unlockScrollOnFocus() {
        this.lockScroll--;
    }
    getShortcutContext() {
        return {
            ElementType: this.getElementType(),
            VisibleIndex: this.getVisibleIndex()
        };
    }
}
